import {BrowserModule} from "@angular/platform-browser";
import {ErrorHandler, NgModule} from "@angular/core";
import {IonicApp, IonicErrorHandler, IonicModule} from "ionic-angular";
import {SplashScreen} from "@ionic-native/splash-screen";
import {StatusBar} from "@ionic-native/status-bar";

import {MyApp} from "./app.component";
import {HomePage} from "../pages/home/home";
import {VideoPage} from "../pages/video/video";
import {PreviewPage} from "../pages/preview/preview";
import {DetailsPage} from "../pages/details/details";
import {ServiceProvider} from "../providers/service/service";
import {TranslateLoader, TranslateModule} from "@ngx-translate/core";
import {TranslateHttpLoader} from "@ngx-translate/http-loader";
import {HttpClient, HttpClientModule} from "@angular/common/http";
import {PdfViewerModule} from "ng2-pdf-viewer";
import {Ionic2RatingModule} from "ionic2-rating";
import {EducationAppComponent} from "../components/education-app/education-app";
import {EducationDetailComponent} from "../components/education-detail/education-detail";

// // The translate loader needs to know where to load i18n files
// // in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    VideoPage,
    PreviewPage,
    DetailsPage,
    EducationAppComponent,
    EducationDetailComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    PdfViewerModule,
    Ionic2RatingModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp, {}, {
      links: [
        {component: VideoPage, name: 'Video', segment: 'video/:id'},
        {component: PreviewPage, name: 'Preview', segment: 'preview/:id/:type'}
      ]
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    VideoPage,
    PreviewPage,
    DetailsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ServiceProvider
  ]
})
export class AppModule {
}



