export class Detail {
  id: number;
  url: string; // 视频播放地址或者在线预览pdf地址
  name: string; // 标题
  rate: number = 0; // 评分
  count: number; // 学习人数
  description: string; // 课程描述

  teacherName: string; // 视频类型有教师名字
  teacherAvatar: string; // 视频类型有教师头像
  teacherInfo: string; // 视频类型有教师介绍
}



